$mainFont: 'Open Sans';
$previewFont: 'Montserrat';
$fontLandingTitle: 'Merriweather';
$fontBody: 'Open Sans';
$fontTitle: 'Montserrat';
$colorIcons: #e25345;
$colorLogo: #114e4e;
$colorBody: #54585a;
$colorFooter: #6b6b6b;
$colorPaletteOne1: #114e4e;
$colorPaletteOne2: #438792;
$colorPaletteOne3: #a2deaf;
$colorPaletteTwo1: #420101;
$colorPaletteTwo2: #bd1010;
$colorPaletteTwo3: #e95626;
$colorPaletteThree1: #3e5b65;
$colorPaletteThree2: #eab052;
$colorPaletteThree3: #a0c0cf;
$colorPaletteFour1: #f38181;
$colorPaletteFour2: #00dffc;
$colorPaletteFour3: #fce38a;
$colorBackgroundImage: linear-gradient(to bottom, #f5f5f5, #ffffff 9%);

//DARK-MODE STYLE
$colorBgDark: #202020;
$colorInputDark: #121212;
$colorBorderDarkMode: white;
$colorFontDarkMode: rgba(255, 255, 255, 0.87);



.font__landing--title {
  font-family: $fontLandingTitle;
  font-size: 19.2px;
  color: $colorIcons;
}

.font__subtitle {
  font-family: $fontBody;
  font-size: 12.8px;
  color: $colorLogo;
}

.font__icons {
  font-family: $fontBody;
  font-size: 12.8px;
  color: $colorBody;
}

.font__footer {
  font-family: $fontBody;
  font-size: 14px;
  color: $colorFooter;
}

.font__form {
  font-size: 18px;
  font-family: $fontBody;
  font-weight: 600;
  background-image: $colorBackgroundImage;
}

.form__font--labels {
  font-family: $fontBody;
  font-size: 14px;
  color: $colorLogo;
  line-height: 20px;
}

.font__preview--title {
  font-family: $fontTitle;
  font-size: 18px;
  font-weight: 700;
  line-height: 20px;
  color: $colorLogo;
}

.font__preview--subtitle {
  font-family: $fontTitle;
  font-size: 16px;
  line-height: 20px;
  color: $colorBody;
}

.button__hover--styles {
  &:hover {
  opacity: 0.6;
  transition: opacity 0.5s;
  }
  &:active {
  opacity: 0.3; 
  }
  cursor: pointer;
}
